import React, { useState, useMemo } from 'react';
import io from 'socket.io-client';
import { Link } from 'react-router-dom';

import history from '@core/history';
import styles from './MainPage.scss';
import GameBoard from '@components/GameBoard/GameBoard';
import { dispatch } from '@src/redux/store';
import { updateBoard, setPlayer } from '@src/redux/game';

function MainPage() {
  const socket = useMemo(() => io(), []);

  console.log(socket.connected); // false

  socket.on('connect', () => {
    console.log(socket.connected); // true
  });

  socket.on('disconnect', () => {
    console.log(socket.connected); // false
  });

  socket.on('boardUpdated', data => {
    console.log('board updated!');
    dispatch(updateBoard(data));
  });

  socket.on('initGame', playerId => {
    console.log('playerId: ', playerId);
    dispatch(setPlayer({ socket, playerId }));
    history.push('/chess/online');
  });

  return (
    <div className={styles.wrapper}>
      <Link to="/chess/local">
        <button className={styles.menuButton}>Play Local</button>
      </Link>
      <button className={styles.menuButton}>Find Online Match</button>
    </div>
  );
}

export default MainPage;
